import { Util } from "@/config/util";

/**
 * 编辑/拉取个人中心编辑页面
 * @param {*} data 
 * @param {*} callback 
 */
export const getUserinfo = function (data, callback) {
    Util.DataEncSubmit('user_info.php', data, (resp) => {
        if (callback && typeof callback === "function") {
            callback(resp);
        }
    });
};

/**
 * 创建店铺
 * @param {*} data 
 * @param {*} callback 
 */
export const createShop = function (data, callback) {
    Util.DataEncSubmit('shop_save.php', data, (resp) => {
        if (callback && typeof callback === "function") {
            callback(resp);
        }
    });
};


