<template>
  <div id="userinfo-edtor">
        <div class="title clearfix">
            <span class="header left">个人信息</span>
        </div>
        <div class="editor-content">
            <div class="info-item clearfix phone">
                <div class="info-title left">
                  <span>登录手机号</span>
                </div>
                <div class="info-content left">
                {{phone}}
                </div>
            </div>

            <div class="info-item clearfix name">
              <div class="info-title left">
                <span class="must">姓名</span>
              </div>
              <div class="info-content left">
                <el-input 
                :class="{errtip: isShowNameEmptyErrtip}" 
                v-model="name"
                @change="clearTip"></el-input>
              </div>
              <div class="errtip-text left" v-if="isShowNameEmptyErrtip">
                请输入名字
              </div>
            </div>

            <div class="info-item clearfix sex">
              <div class="info-title left">
                <span class="must">性别</span>
              </div>
              <div class="info-content left">
                <el-radio-group v-model="sex" @change="clearTip">
                  <el-radio 
                  v-for="item in sexOptions" 
                  :label="item.value"
                  :key="item.value"
                  >{{item.label}}</el-radio>
                </el-radio-group>
              </div>
            </div>

            <div class="info-item clearfix identity">
              <div class="info-title left">
                <span class="">身份证号码</span>
              </div>
              <div class="info-content left">
                <el-input 
                :class="{errtip: isShowIdentityEmptyErrtip}" 
                v-model="identity"
                @change="clearTip"></el-input>
              </div>
              <div class="errtip-text left" v-if="isShowIdentityEmptyErrtip">
                {{IdentityErrTip}}
              </div>
            </div>

            <div class="info-item clearfix health">
              <div class="info-title left">健康证扫描件</div>
              <div class="info-content left">
                <img v-if="healthImg === ''" src="../images/symbols-empty-pic_pic_normal.png">
                <div v-else class="selec-img">
                  <img  :src="imgbase_url + '/img_get.php?img=1&height=200&width=120&imgname=' + healthImg">
                  <span class="picture-actions">
                    <span class="picture-preview">
                      <i class="el-icon-view" @click="isshowPicture"></i>
                    </span>
                    <span class="picture-delete">
                      <i class="el-icon-delete2" @click="deleteimg"></i>
                    </span>
                  </span>
                </div>
              </div>

              <!-- 图片上传  start-->
              <div class="img-upload left">
                <s-upload :ImageSize="imageSize" @send-image="imgChangeHandler"></s-upload>
                <div class="img-tip">注：支持JPG、JPEG、PNG文件格式，大小不得大于500KB</div>
              </div>
              <!-- 图片上传 end-->
            </div>

            <div class="info-item clearfix wx">
              <div class="info-title left">绑定微信</div>
              <div class="info-content left">
                {{isBindWexin.toString(wexin)}}
              </div>
              <div v-if="wexin === isBindWexin.YES" class="left bind-btn" @click="wxunbindTipOpen">解绑</div>
              <div v-else class="left bind-btn" @click="wxbindTipOpen">绑定</div>
            </div>

            <div class="btn-group">
              <div class="btn btn-confrim" @click="confirmChagne">确定</div>
              <div class="btn btn-cancel" @click="handleClose">取消</div>
            </div>
        </div>

        <!-- 图片放大 -->
        <show-img v-if="showPicture" @close-img="isclosePicture" :imgSrc="imgSrc"></show-img>
        <!-- 图片放大 -->  

        <!-- 微信解绑弹窗 -->
        <div class="wxUnbindTip" v-if="isShowUnbindTip">
          <div class="wxdialog-layer" @click="wxunbindTipClose"></div>
          <div class="wxdialog-content">
            <div></div>
            <!-- 提示 -->
            <div class="dialog-content-confirm" v-if="isShowUnbindWxTip">
                <div class="tip-text clearfix">
                  <img class="left" src="../images/question_img_normal.png" alt="">
                  <span class="left">解绑前需要身份确认，确定解绑吗？</span>
                </div>
                <div class="btn-group-wx">
                  <div class="btn btn-confrim" @click="unbindWxTipConfirm">确定</div>
                  <div class="btn btn-cancel" @click="wxunbindTipClose">取消</div>
                </div>
            </div>
            <!-- 二维码 -->
            <div v-if="isShowUnbindWxCode" class="wximg-code">
              <img :src="wxcodeUnbind">
            </div>
            <!-- 解绑成功 -->
            <div class="dialog-content-unbindsuc" v-if="isShowWxUnbindSucTip">
                <div class="tip-text clearfix">
                  <img class="left" src="../images/success_img_normal.png" alt="">
                  <span class="left">微信解绑成功</span>
                </div>
                <div class="btn-group-wx">
                  <div class="btn btn-confrim" @click="unbindWxScuHandler">绑定其他微信</div>
                  <div class="btn btn-cancel" @click="wxunbindTipClose">取消</div>
                </div>
            </div>
            <!-- 解绑失败提示 -->
            <div class="dialog-content-unbindsuc" v-if="isShowWxUnbindFailTip">
                <div class="tip-text clearfix">
                  <img class="left" src="../images/fail_img_normal.png" alt="">
                  <span class="left">微信解绑失败</span>
                </div>
                <div class="btn-group-wx">
                  <div class="btn btn-confrim" @click="wxunbindFailHandler">重新扫码</div>
                  <div class="btn btn-cancel" @click="wxunbindTipClose">取消</div>
                </div>
            </div>
          </div>
        </div>
        <!-- 微信解绑弹窗 -->

        <!-- 微信绑定弹窗 -->
        <div class="wxUnbindTip" v-if="isShowbindTip">
          <div class="wxdialog-layer" @click="wxbindTipClose"></div>
          <div class="wxdialog-content">
            <div></div>
            <!-- 提示 -->
            <div class="dialog-content-confirm" v-if="isShowbindWxTip">
                <div class="tip-text clearfix">
                  <img class="left" src="../images/question_img_normal.png" alt="">
                  <span class="left">绑定前需要身份确认，确定绑定吗？</span>
                </div>
                <div class="btn-group-wx">
                  <div class="btn btn-confrim" @click="bindWxTipConfirm">确定</div>
                  <div class="btn btn-cancel" @click="wxunbindTipClose">取消</div>
                </div>
            </div>
            <!-- 二维码 -->
            <div v-if="isShowbindWxCode" class="wximg-code">
              <img :src="wxcodebind">
            </div>
            <!-- 解绑成功 -->
            <div class="dialog-content-unbindsuc" v-if="isShowWxbindSucTip">
                <div class="tip-text clearfix">
                  <img class="left" src="../images/success_img_normal.png" alt="">
                  <span class="left">微信绑定成功</span>
                </div>
                <div class="btn-group-wx  wx-success">
                  <div class="btn btn-confrim" @click="wxbindTipClose">关闭</div>
                </div>
            </div>
            <!-- 解绑失败提示 -->
            <div class="dialog-content-unbindsuc" v-if="isShowWxbindFailTip">
                <div class="tip-text clearfix">
                  <img class="left" src="../images/fail_img_normal.png" alt="">
                  <span class="left">微信绑定失败</span>
                </div>
                <div class="btn-group-wx">
                  <div class="btn btn-confrim" @click="wxbindTipClose">确定</div>
                  <div class="btn btn-cancel" @click="wxbindTipClose">取消</div>
                </div>
            </div>
          </div>
        </div>
        <!-- 微信绑定弹窗 -->
  </div>
</template>

<script>
import SUpload from "@/components/upload/SUpload";
import ShowImg from "@/components/showImg";

import { getUserinfo } from "../api";
import { isBindWexin, CustomSex, errcode } from "@/config/cfg";
import { Public } from "@/config/public";
import { Util } from "@/config/util";

export default {
  components: {
    SUpload,
    ShowImg
  },
  data() {
    var a = {
      phone: "",
      name: "",
      identity: "",
      wexin: 1,

      sex: 1,
      sexOptions: [
        {
          value: CustomSex.MAN,
          label: CustomSex.toString(CustomSex.MAN)
        },
        {
          value: CustomSex.FAMEN,
          label: CustomSex.toString(CustomSex.FAMEN)
        },
        {
          value: CustomSex.SECRECY,
          label: CustomSex.toString(CustomSex.SECRECY)
        }
      ],

      imgbase_url: imgbase_url,
      healthImg: "",

      imageSize: 512,
      showPicture: false,
      imgSrc: "",

      token: "",
      key: "",

      isBindWexin: isBindWexin,

      isShowNameEmptyErrtip: false,
      isShowIdentityEmptyErrtip: false,
      IdentityErrTip: "",

      wxcodeUnbind: "", //解绑二维码图片
      isShowUnbindTip: false, //微信解绑弹窗
      isShowUnbindWxTip: false, //显示解绑提示
      isShowUnbindWxCode: false, //显示二维码
      isShowWxUnbindSucTip: false, //解绑成功提示
      isShowWxUnbindFailTip: false, //解绑失败提示

      wxcodebind: "", //绑定二维码图片
      isShowbindTip: false, //微信绑定弹窗
      isShowbindWxTip: false, //显示绑定提示
      isShowbindWxCode: false, //显示二维码
      isShowWxbindSucTip: false, //绑定成功提示
      isShowWxbindFailTip: false //绑定失败提示
    };
    window.a = a;
    return a;
  },
  created() {
    this.token = Util.creatToken();
    this.key = Util.creatKey();
    this.init();
    this.wximginit();
  },
  watch: {
    isShowUnbindWxTip: function(val) {
      if (val === true) {
        this.unbindmsg_reg();
      }
    },
    isShowbindWxCode: function(val) {
      if (val === true) {
        this.bindmsg_reg();
      }
    }
  },
  mounted() {
    window.WebSock.Init(
      "ws://srv.jzzwlcm.com:13010/websocket",
      this.token,
      this.key,
      () => {
        // this.msg_reg();
      }
    );
  },
  methods: {
    imgChangeHandler(select) {
      this.healthImg = select;
    },
    deleteimg() {
      this.healthImg = "";
    },
    isshowPicture() {
      this.imgSrc = this.healthImg;
      this.showPicture = true;
    },
    isclosePicture() {
      this.showPicture = false;
    },
    confirmChagne() {
      if (this.check()) {
        return;
      }
      let data = {
        edit_user_info: 1,
        real_name: this.name,
        health_certificate: this.healthImg,
        is_weixin: this.wexin,
        sex: this.sex
      };
      // if (this.identity !== "") {
        data["identity"] = this.identity;
      // }
      getUserinfo(data, resp => {
        if (resp.ret === 0) {
          this.$router.push("/personalcenter/userinfo");
        } else if (resp.ret === errcode.IDCARD_ERR) {
          this.isShowIdentityEmptyErrtip = true;
          this.IdentityErrTip = errcode.toString(errcode.IDCARD_ERR);
          console.warn("保存失败！");
        } else {
          console.warn("保存失败！");
        }
      });
    },
    handleClose() {
      this.$router.push("/personalcenter/userinfo");
    },
    init() {
      let data = {
        get_user_info: 1
      };

      getUserinfo(data, resp => {
        if (resp.ret === 0) {
          let userinfo = resp.data.userinfo || {};

          this.phone = userinfo.phone;
          this.name = userinfo.real_name;
          this.identity = userinfo.identity;
          this.healthImg = userinfo.health_certificate || "";
          this.wexin = userinfo.is_weixin;
          this.sex = userinfo.sex;
        } else {
          console.warn("获取基本信息失败！");
        }
      });
    },
    //微信解绑
    wxunbindTipClose() {
      this.isShowUnbindTip = false;
    },
    wxunbindTipOpen() {
      this.isShowUnbindTip = true;
      this.clearAllDialogTip();
      this.isShowUnbindWxTip = true;
    },
    unbindWxTipConfirm() {
      this.clearAllDialogTip();
      this.isShowUnbindWxCode = true;
    },
    unbindWxScuHandler() {
      this.isShowUnbindTip = false;
      this.wxbindTipOpen();
    },
    wxunbindFailHandler() {
      this.clearAllDialogTip();
      this.isShowUnbindWxCode = true;
    },
    wximginit() {
      let userid = Public.getUserid();
      let token = Util.creatToken();

      this.wxcodeUnbind =
        imgbase_url +
        "/img_get.php?get_binding_qrcode=1&userid=" +
        userid +
        "&type=" +
        isBindWexin.NO +
        "&token=" +
        token;
      this.wxcodebind =
        imgbase_url +
        "/img_get.php?get_binding_qrcode=1&userid=" +
        userid +
        "&type=" +
        isBindWexin.YES +
        "&token=" +
        token;
    },
    //微信扫码解绑
    unbindmsg_reg() {
      let token = Util.creatToken();
      let that = this;

      Util.RecWxUnbindinfo(token, resp => {
        if (resp.ret === 0 && resp.type === isBindWexin.NO) {
          that.clearAllDialogTip();
          that.isShowWxUnbindSucTip = true;
          that.init();
        } else {
          console.log(isBindWexin.NO);
          that.clearAllDialogTip();
          that.isShowWxUnbindFailTip = true;
        }
      });
    },
    //微信扫码绑定
    bindmsg_reg() {
      let token = Util.creatToken();
      let that = this;

      Util.RecWxBindinfo(token, resp => {
        if (resp.ret === 0 && resp.type === isBindWexin.YES) {
          that.clearAllDialogTip();
          that.isShowWxbindSucTip = true;
          that.init();
        } else {
          that.clearAllDialogTip();
          that.isShowWxbindFailTip = true;
        }
      });
    },
    clearAllDialogTip() {
      this.isShowUnbindWxTip = false; //显示解绑提示
      this.isShowUnbindWxCode = false; //显示二维码
      this.isShowWxUnbindSucTip = false; //解绑成功提示
      this.isShowWxUnbindFailTip = false; //解绑失败提示
      this.isShowbindWxTip = false; //显示绑定提示
      this.isShowbindWxCode = false; //显示二维码
      this.isShowWxbindSucTip = false; //绑定成功提示
      this.isShowWxbindFailTip = false; //绑定失败提示
    },
    // 微信解绑
    wxbindTipClose() {
      this.isShowbindTip = false;
    },
    wxbindTipOpen() {
      this.isShowbindTip = true;
      this.clearAllDialogTip();
      this.isShowbindWxTip = true;
    },
    bindWxTipConfirm() {
      this.clearAllDialogTip();
      this.isShowbindWxCode = true;
    },
    clearTip() {
      this.isShowNameEmptyErrtip = false;
      this.isShowIdentityEmptyErrtip = false;
    },
    check() {
      let Bool = false;
      if (this.name === "") {
        this.isShowNameEmptyErrtip = true;
        Bool = true;
      }
      // if (this.identity === "") {
      //   this.isShowIdentityEmptyErrtip = true;
      //   this.IdentityErrTip = "请输入身份证";
      //   Bool = true;
      // }
      return Bool;
    }
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";

#userinfo-edtor {
  @include fc(14px, #333);
  background-color: #fff;
  box-shadow: 0 2px 4px 0 #becaeb;
}

.title {
  @include fc(14px,#666);
  height: 40px;
  line-height: 40px;
  background: #f6f8fc;
  .header {
    padding-left: 14px;
  }
  .edit {
    cursor: pointer;
    position: relative;
    color: $blue;
    padding-right: 20px;
    &:before {
      position: absolute;
      left: -20px;
      top: 10px;
      content: "";
      @include wh(20px, 20px);
      background: url("../images/edit.png")no-repeat;
    }
  }
}
.editor-content {
  position: relative;
}

.info-title {
  width: 220px;
  text-align: right;
  opacity: 0.5708;
  margin-right: 14px;
}
.info-content {
  @include fc(14px, #666);
  text-align: justify;
  word-break: break-all;
}
.info-item {
  padding-bottom: 27px;
}
.info-item.phone {
  padding-top: 48px;
  line-height: 19px;
}
.info-item.name {
  line-height: 34px;
  .info-title {
    line-height: 34px;
  }
}
.info-item.identity {
  padding-bottom: 22px;
  line-height: 34px;
  .info-title {
    line-height: 34px;
  }
}
.info-item.health {
  padding-bottom: 22px;
  .info-content {
    @include wh(200px, 120px);
    img {
      @include wh(200px, 120px);
    }
  }
  .img-upload {
    margin-left: 20px;
    .img-tip {
      font-size: 12px;
      color: #9b9b9b;
      padding-top: 74px;
    }
  }
}
.info-item.wx {
  padding-bottom: 300px;
  line-height: 30px;
  .bind-btn {
    @include wh(60px, 30px);
    border: 1px solid #4877e7;
    border-radius: 2px;
    font-size: 14px;
    color: #4877e7;
    cursor: pointer;
    text-align: center;
    line-height: 30px;
    margin-left: 20px;
  }
}
.selec-img {
  position: relative;
  @include wh(200px, 120px);
}
.picture-actions {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  cursor: default;
  text-align: center;
  color: #fff;
  opacity: 0;
  font-size: 20px;
  background-color: rgba(0, 0, 0, 0.5);
  transition: opacity 0.3s;

  span {
    line-height: 120px;
  }
  .el-icon-view {
    cursor: pointer;
  }
  .el-icon-delete2 {
    cursor: pointer;
  }
  .picture-delete {
    margin-left: 10px;
  }
}
.selec-img:hover {
  .picture-actions {
    opacity: 1;
  }
}
.btn-group {
  position: absolute;
  bottom: 50px;
  left: 50%;
  transform: translateX(-50%);

  .btn {
    @include wh(160px,40px);
    line-height: 40px;
    text-align: center;
    border-radius: 2px;
    font-size: 14px;
    color: #4877e7;
    display: inline-block;
    border: 1px solid #4877e7;
    cursor: pointer;
  }
  .btn-confrim {
    background: #4877e7;
    color: #fff;
    margin-right: 56px;
  }
}
.wxUnbindTip {
  position: fixed;
  /* width: 100%;
  height: 100%; */
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1499;

  .wxdialog-layer {
    position: fixed;
    z-index: 1003;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.7);
    width: 100%;
    height: 100%;
    overflow: hidden;
    transition: all 0.5s;
  }
  .wxdialog-content {
    @include wh(540px, 280px);
    background-color: #fff;
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 1500;
    border-top: 4px solid #5a8cff;
  }
}
.dialog-content-confirm {
  @include wh(100%, 100%);
  position: relative;
  .tip-text {
    img {
      @include wh(50px, 50px);
    }
    span {
      margin-left: 20px;
    }
    padding-left: 119px;
    padding-top: 80px;
    height: 50px;
    line-height: 50px;
    font-size: 14px;
    color: #666666;
  }
}
.wximg-code {
  @include wh(100%, 100%);
  position: relative;
  img {
    @include wh(200px, 200px);
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-100px, -100px);
  }
}
.dialog-content-unbindsuc {
  @include wh(100%, 100%);
  position: relative;
  .tip-text {
    img {
      @include wh(50px, 50px);
    }
    span {
      margin-left: 20px;
    }
    padding-left: 192px;
    padding-top: 80px;
    height: 50px;
    line-height: 50px;
    font-size: 14px;
    color: #666666;
  }
}

.btn-group-wx {
  position: absolute;
  bottom: 50px;
  left: 80px;

  .btn {
    @include wh(160px,40px);
    line-height: 40px;
    text-align: center;
    border-radius: 2px;
    font-size: 14px;
    color: #4877e7;
    display: inline-block;
    border: 1px solid #4877e7;
    cursor: pointer;
  }
  .btn-confrim {
    background: #4877e7;
    color: #fff;
    margin-right: 56px;
  }
}
.btn-group-wx.wx-success {
  left: 190px;
}

span.must {
  position: relative;
}
span.must:before {
  position: absolute;
  left: -10px;
  top: 3px;
  content: "";
  display: block;
  @include wh(10px, 10px);
  background: url(../images/must.png);
  background-repeat: no-repeat;
  margin-right: 4px;
}
.errtip-text {
  @include fc(12px, #e7487e);
  margin-left: 20px;
}
</style>

<style lang="scss">
@import "src/styles/mixin.scss";
#userinfo-edtor {
  .el-input__inner {
    @include wh(280px, 34px);
    border-radius: 0;
  }
  .el-input.errtip .el-input__inner {
    border-color: #e7487e;
  }
}
</style>





